.wallet-box{
  display: flex !important;
  justify-content: flex-end;
  max-width: 420px;
  width: calc(100% - 20px);
  margin: 0 auto;
  padding: 10px 0;
}

.swap{
  margin: 0 auto;
  padding: 30px 15px;
  background-color: #333;
  max-width: 420px;
  width: calc(100% - 20px);
  border-radius: 10px;
  color: #fff;
}

.swap-input{
  background-color: #262626;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.swap-item h3{
  font-size: 16px;
  margin-bottom: 10px;
}

.swap-item-title{
  display: flex;
  justify-content: space-between;
}

.swap-input-input{
  flex: 1;
  border: 0;
  background-color: transparent;
  outline: 0;
  font-size: 18px;
  color: #fff;
  text-align: right;
  font-weight: bold;
  margin-right: 10px;
}

.ant-select-selection-item{
  font-size: 18px;
  display: flex;
  align-items: center;
}

.swap-input .ant-select{
  width: 100px;
}

.swap-token-select{

}

.swap-change{
  margin: 10px auto;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, .35);
  color: rgba(255, 255, 255, .35);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.swap-change svg{
  fill: rgba(255, 255, 255, .35);
}

.swap-button{
  margin-top: 20px;
  width: 100%;
  height: 50px;
  line-height: 50px;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  background-color: #111;
  cursor: pointer;
}

.swap-receive{
  flex: 1;
  text-align: right;
  margin-right: 10px;
  font-size: 18px;
  font-weight: bold;
}